.contact {
	@include flex(row, null, null, wrap);
	@include mq($max-width: 1200px) {
		flex-wrap: wrap-reverse; }
	&-left {
		width: 54.79%;
		@include mq($max-width: 1200px) {
			width: 100%; } }
	&-right {
		background: white;
		width: 45.21%;
		padding: 65px 75px;
		@include mq($max-width: 1550px) {
			padding: 60px 30px; }
		@include mq($max-width: 1200px) {
			width: 100%;
			padding: 30px 15px;
			@include flex(column, null, end, nowrap); }
		@include mq($max-width: 575px) {
			padding: 15px; } }
	.row {
		@include flex(row, null, center, wrap);
		margin: 0 0 27px 0; }
	#map {
		display: block;
		width: 100%;
		height: 534px;
		@include mq($max-width: 1550px) {
			height: 445px; }
		@include mq($max-width: 1200px) {
			height: 320px; } }
	.entry-form {
		margin: 52px 0 0 0;
		@include mq($max-width: 700px) {
			margin: 20px 0 0 0; } }
	.block-title {
		font-size: 43px;
		@include mq($max-width: 575px) {
			font-size: 35px; }
		span {
			margin: 0 0 0 50px;
			@include mq($max-width: 575px) {
				margin: 0; } } }
	&__label {
		display: block;
		width: 196px;
		height: auto;
		border-radius: 13px;
		margin: 0 0 0 34px;
		box-shadow: 0px 4px 25px rgba(0, 0, 0, 0.15);
		@include mq($max-width: 1400px) {
			margin: 0 0 0 20px; }
		@include mq($max-width: 575px) {
			margin: 15px 0 0 0; } } }
