.header {
	background: $body-bg;
	position: relative;
	z-index: 3;
	@include mq($max-width: 1200px) {
		background: white; }
	.container {
		max-width: 100%;
		@include flex(row, between, center, wrap);
		padding: 0 50px 0;
		position: relative;
		@include mq($max-width: 1800px) {
			padding: 0 15px 0; }
		@include mq($max-width: 1200px) {
			padding: 15px; }
		@include mq($max-width: 575px) {
			padding: 15px 10px; } }
	.main-nav {
		@include mq($min-width: 320px) {
			@include flex(row, between, center, wrap);
			// position: relative
			padding: 0;
			@include trs; }
		@include mq($max-width: 1200px) {
			order: 0; } }
	#main-menu-state:not(:checked) ~ #main-menu {
		@include mq($max-width: 1200px) {
			display: none; } }
	#main-menu-state:checked ~ #main-menu {
		// +mq($max-width: 1200px)
 }		/// +flex(row, null, center, wrap)
	#main-menu {
		@include mq($min-width: 320px) {
			padding: 0;
			position: absolute;
			left: 0;
			top: 100%;
			width: 100%;
			border-radius: 0; }
		@include mq($min-width: 1200px) {
			@include flex(row, null, center, wrap);
			margin: 0;
			padding: 0;
			border-radius: 0;
			border: none;
			background: none;
			box-shadow: none;
			width: auto;
			position: static; }
		ul {
			@include mq($min-width: 320px) {
				margin: 0;
				padding: 0;
				border-radius: 0;
				border: none;
				background: none;
				box-shadow: none;
				line-height: 1;
				opacity: 1;
				&:before,
				&:after {
					display: none; } } }
		li {
			@include mq($min-width: 1200px) {
				margin: 0;
				border-radius: 0;
				padding: 0;
				border: none;
				background: none;
				box-shadow: none;
				line-height: 1;
				opacity: 1;
				&:before,
				&:after {
					display: none; } } }
		a {
			@include mq($min-width: 1200px) {
				background: none;
				margin: 0;
				border-radius: 0;
				padding: 0;
				border: none;
				box-shadow: none;
				line-height: 1;
				opacity: 1;
				color: black; }
			&.has-submenu {
				position: relative;
				&[aria-expanded="true"] {
					.sub-arrow {
						&:before {
							content: '-'; } } } } }
		.sub-arrow {
			@include mq($max-width: 1200px) {
				width: 30px;
				height: 30px;
				top: 50%;
				right: 10px;
				transform: translate(0, -50%);
				border: 1px solid $dark;
				position: absolute;
				margin: 0;
				z-index: 2;
				&:before {
					content: '+';
					display: block;
					font-size: 15px;
					line-height: 30px; } }
			@include mq($min-width: 1200px) {
				right: 2px; }
			@include mq($max-width: 1800px) {
				margin: -2px 0 0 0; } }
		ul {
			min-width: 100% !important;
			box-shadow: 0px 4px 15px rgba(0, 0, 0, 0.15);
			padding: 15px;
			@include mq($min-width: 320px) {
				position: static;
				background: white; }
			@include mq($min-width: 1200px) {
				position: absolute; }
			@include mq($max-width: 1200px) {
				padding: 0 15px; }
			& > li {
				& > a {
					display: inline-block;
					font-size: 18px;
					padding: 8px 5px;
					color: $dark2;
					@include mq($min-width: 320px) {
						padding: 10px; }
					@include mq($max-width: 1700px) {
						font-size: 15px; }
					@include mq($max-width: 1350px) {
						font-size: 14px; }
					&::after {
						content: '';
						display: block;
						width: 0;
						@include trs;
						height: 1px;
						background: $dark2;
						margin: 2px 0 0 0; }
					&:hover {
						font-weight: 700;
						&::after {
							width: 100%; } } } } }
		& > li {
			position: relative;
			@include mq($min-width: 320px) {
				width: 100%; }
			@include mq($min-width: 1200px) {
				width: auto; }
			& > a {
				padding: 50px 20px;
				font-size: 18px;
				color: $dark2;
				position: relative;
				@include mq($max-width: 1800px) {
					padding: 45px 15px; }
				@include mq($max-width: 1700px) {
					font-size: 15px; }
				@include mq($max-width: 1600px) {
					padding: 30px 15px; }
				@include mq($max-width: 1350px) {
					font-size: 14px; }
				@include mq($max-width: 1200px) {
					padding: 15px 15px; }
				@include mq($max-width: 575px) {
					padding: 15px 45px 15px 15px; }
				&::after {
					content: '';
					display: block;
					width: 0;
					@include trs;
					height: 6px;
					background: $dark2;
					margin: 2px 0 0 0;
					position: absolute;
					bottom: 0;
					left: 0;
					@include mq($max-width: 1200px) {
						display: none; } }
				&:hover {
					@include mq($min-width: 1200px) {
						font-weight: 700; }
					&::after {
						width: 100%; } } } } }

	.btn-entry {
		@include mq($max-width: 1200px) {
			order: -1;
			margin: 0 10px 0 0; }
		@include mq($max-width: 575px) {
			padding: 10px; }
		svg {
			@include mq($max-width: 575px) {
				margin: 0; } }
		span {
			@include mq($max-width: 575px) {
				display: none; } } }
	.logo {
		@include mq($max-width: 1200px) {
			order: -1; }
		&__img {
			@include mq($max-width: 400px) {
				max-width: 90px; } }
		&__text {
			@include mq($max-width: 1600px) {
				display: none; } } }

	.choose-tel {
		@include flex(row, between, center, wrap);
		position: relative;
		@include mq($max-width: 1500px) {
			flex-direction: column-reverse;
			align-items: flex-start; }
		@include mq($max-width: 1200px) {
			order: -1;
			margin: 0 10px 0 auto; }
		.city {
			margin: 0 36px 0 0;
			&__curr {
				font-size: 15px;
				font-family: 'UniBook', sans-serif;
				position: relative;
				cursor: pointer;
				@include mq($max-width: 575px) {
					font-size: 14px; }
				&::after {
					position: absolute;
					top: 50%;
					right: -20px;
					transform: translate(0, -40%);
					@include triangle(down, 6px, $dark); } }
			&-list {
				padding: 5px;
				border-radius: 4px;
				background: white;
				list-style-type: none;
				padding: 0;
				position: absolute;
				left: 0;
				top: 100%;
				width: 100%;
				display: none;
				li {
					font-size: 16px;
					padding: 5px 10px;
					cursor: pointer;
					@include mq($max-width: 1200px) {
						font-size: 15px; }
					@include mq($max-width: 575px) {
						font-size: 14px; }
					&.active {
						display: none; } } } }
		.phone {
			&__curr {
				font-size: 24px;
				color: $dark2;
				@include mq($max-width: 1700px) {
					font-size: 18px; }
				@include mq($max-width: 800px) {
					font-size: 15px; }
				@include mq($max-width: 575px) {
					font-size: 14px; }
				span {
					font-weight: 700; } }
			&-list {
				padding: 5px;
				border-radius: 4px;
				background: white;
				list-style-type: none;
				padding: 0;
				position: absolute;
				left: 0;
				width: 100%;
				display: none;
				li {
					font-size: 14px;
					padding: 2px;
					&.active {
						display: none; } } } } } }

