.recommend {
	padding: 45px 0 126px;
	@include mq($max-width: 1650px) {
		padding: 45px 0; }
	.container {
		max-width: 1480px;
		@include flex(row, between, start, wrap);
		@include mq($max-width: 1650px) {
			max-width: 1170px; } }
	&-left {
		@include flex(column, null, center, nowrap);
		max-width: 496px;
		width: 100%;
		background: white;
		padding: 65px 48px;
		border-radius: 10px;
		box-shadow: 0px 4px 25px rgba(0, 0, 0, 0.15);
		@include mq($max-width: 1650px) {
			max-width: 350px;
			padding: 30px; }
		@include mq($max-width: 1200px) {
			max-width: 640px;
			margin: 0 auto; }
		@include mq($max-width: 575px) {
			padding: 15px; } }
	&__img-text {
		margin: 37px 0 0 auto;
		@include mq($max-width: 1650px) {
			margin: 15px 0 0 auto; }
		@include mq($max-width: 700px) {
			margin: 10px auto; }
		@include mq($max-width: 575px) {
			max-width: 100%; } }
	&__title {
		text-align: center;
		width: 100%;
		b {
			font-size: 90px;
			font-weight: 500;
			font-style: italic;
			line-height: 1;
			font-family: 'PF', sans-serif;
			@include mq($max-width: 1650px) {
				font-size: 70px; }
			@include mq($max-width: 700px) {
				font-size: 40px;
				display: block; } }
		span {
			font-family: 'UniBook', sans-serif;
			font-size: 46px;
			@include mq($max-width: 1650px) {
				font-size: 30px; }
			@include mq($max-width: 700px) {
				font-size: 25px; } }
		i {
			font-size: 40px;
			font-style: italic;
			font-family: 'PF', sans-serif;
			@include mq($max-width: 1650px) {
				font-size: 30px; }
			@include mq($max-width: 700px) {
				font-size: 25px; } } }
	.entry-form.vertical {
		margin: 55px 0 0 0;
		@include mq($max-width: 1200px) {
			margin: 15px 0 0 auto; } }
