	&-right {
		max-width: 761px;
		width: 100%;
		margin: 0 80px;
		@include mq($max-width: 1650px) {
			max-width: 650px;
			margin: 0 40px; }
		@include mq($max-width: 1200px) {
			margin: 20px auto 0 auto; } }
	.slider-for {
		margin: 0 0 50px 0;
		@include mq($max-width: 1650px) {
			margin: 0 0 20px 0; }
		.slide {
			&-link {
				position: relative;
				display: block;
				width: 100%; }
			&__img {
				display: block;
				width: 100%; } } }
	.play {
		position: absolute;
		left: 50%;
		top: 50%;
		transform: translate(-50%, -50%);
		z-index: 2;
		svg {
			display: block;
			width: 177px;
			height: 177px;
			@include mq($max-width: 575px) {
				width: 100px;
				height: 100px; }
			circle {
				opacity: 1;
				@include trs; } }
		&:hover {
			svg {
				circle {
					opacity: 0; } } } }
	.slider-nav {
		position: relative;
		width: calc( 100% + 30px );
		margin: 0 0 0 -15px;
		@include mq($max-width: 1650px) {
			width: calc( 100% + 8px );
			margin: 0 0 0 -4px; }
		@include mq($max-width: 800px) {
			width: 80%;
			margin: 0 auto; }
		@include mq($max-width: 575px) {
			width: calc( 100% - 140px ); }
		.slide {
			cursor: pointer;
			margin: 0 15px;
			@include trs;
			opacity: 0.7;
			@include mq($max-width: 1650px) {
				margin: 0 8px; }
			&.slick-current {
				opacity: 1; }
			&__img {
				display: block;
				width: 100%; }
			&__name {
				text-align: center;
				font-family: 'PF', sans-serif;
				font-size: 18px;
				font-weight: 600;
				margin: 21px 0 0 0;
				@include mq($max-width: 1650px) {
					margin: 10px 0 0 0;
					font-size: 15px; } } } }

	.arrow {
		&-prev {
			left: -70px;
			transform: translate(0, -80%) rotate(180deg);
			@include mq($max-width: 700px) {
				left: -60px; } }
		&-next {
			right: -70px;
			transform: translate(0, -80%);
			@include mq($max-width: 700px) {
				right: -60px; } } } }
